.header {
  padding: 25px 0;
  background-color: #222;
  text-align: center;
}

.header .logo {
  width: 100px;
}

.content {
  margin: 80px 0;
}

.content > h1 {
  text-align: center;
}

.tesla-car {
  position: relative;
  margin: 0 auto;
  width: 80%;
}

.tesla-img {
  display: block;
  width: 100%;
}

.wheel-left {
  display: block;
  width: 10%;
  position: absolute;
  bottom: 28%;
  left: 24.5%;
}

.wheel-right {
  display: block;
  width: 10%;
  position: absolute;
  bottom: 28%;
  right: 26.3%;
}

.models {
  width: 100%;
  white-space: nowrap;
  overflow: auto;
  text-align: center;
  position: relative;
  z-index: 2;
  bottom: 6vw;
}

.model {
  display: inline-block;
  width: 150px;
  text-align: center;
}

.model img {
  height: 15px;
}

.model h1 {
  padding-top: 5px;
  position: relative;
}

.mi {
  font-size: 14px;
  vertical-align: top;
}

.buttons {
  text-align: center;
  margin-top: -3%;
}

.sections {
  padding: 20px 40px;
  display: inline-block;
  border: 2px solid black;
  margin-right: 20px;
  vertical-align: top;
  margin-bottom: 20px;
}

.sections h2 {
  font-size: 16px;
  margin-bottom: 6px;
}

.sections h1 {
  font-size: 48px;
  margin-bottom: 10px;
}

.button-config {
  font-family: "Share Tech Mono", monospace;
  font-size: 30px;
  border: 2px solid black;
  background-color: white;
  display: inline-block;
  width: 40px;
  height: 40px;
  outline: 0;
}

.button-config.dec {
  margin-right: 5px;
}

.button-config.ac {
  width: 120px;
  font-size: 24px;
}

.button-config.wheel {
  width: 55px;
  margin-right: 5px;
}

button {
  transition: 0.3s ease;
}

button:disabled {
  color: grey;
  border: 2px solid grey;
  cursor: not-allowed;
}

.speed-45 {
  -webkit-animation: infinite-spinning 230ms steps(6) infinite;
          animation: infinite-spinning 230ms steps(6) infinite;
}

.speed-50 {
  -webkit-animation: infinite-spinning 200ms steps(6) infinite;
          animation: infinite-spinning 200ms steps(6) infinite;
}

.speed-55 {
  -webkit-animation: infinite-spinning 170ms steps(6) infinite;
          animation: infinite-spinning 170ms steps(6) infinite;
}

.speed-60 {
  -webkit-animation: infinite-spinning 140ms steps(6) infinite;
          animation: infinite-spinning 140ms steps(6) infinite;
}

.speed-65 {
  -webkit-animation: infinite-spinning 110ms steps(6) infinite;
          animation: infinite-spinning 110ms steps(6) infinite;
}

.speed-70 {
  -webkit-animation: infinite-spinning 80ms steps(6) infinite;
          animation: infinite-spinning 80ms steps(6) infinite;
}


@-webkit-keyframes infinite-spinning {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


@keyframes infinite-spinning {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@media screen and (max-width: 768px) {
  .tesla-car {
    width: 100%;
  }

  .models {
    position: static;
  }

  .model {
    width: 120px;
  }

  .buttons {
    margin-top: 0;
  }
  

  .sections {
    padding: 12px 15px;
    margin: 20px 5px 0 5px;
  }
}
